"use client"

/**
 * ============================================================================
 * IOTA CONTRACT INTEGRATION HOOK - MICRO CARBON OFFSET TRACKER
 * ============================================================================
 * 
 * This hook contains ALL the contract interaction logic for Carbon Offset Tracker.
 * 
 * ============================================================================
 */

import { useState, useEffect } from "react"
import {
  useCurrentAccount,
  useIotaClient,
  useSignAndExecuteTransaction,
  useIotaClientQuery,
} from "@iota/dapp-kit"
import { useNetworkVariable } from "@/lib/config"
import type { IotaObjectData } from "@iota/iota-sdk/client"

// ============================================================================
// CONTRACT CONFIGURATION
// ============================================================================

export const CONTRACT_MODULE = "contract" // Your Move module name
export const CONTRACT_METHODS = {
  CREATE: "create",
  ADD_OFFSET: "add_offset",
  TRANSFER_OFFSET: "transfer_offset",
  INCREMENT: "increment",
  SET_VALUE: "set_value",
  GET_TOTAL: "get_total",
  GET_OWNER: "get_owner",
} as const

// ============================================================================
// DATA EXTRACTION
// ============================================================================

function getObjectFields(data: IotaObjectData): { value: number; owner: string } | null {
  if (data.content?.dataType !== "moveObject") {
    console.log("Data is not a moveObject:", data.content?.dataType)
    return null
  }
  
  const fields = data.content.fields as any
  if (!fields) {
    console.log("No fields found in object data")
    return null
  }
  
  // Log the actual structure for debugging
  console.log("Object fields structure:", JSON.stringify(fields, null, 2))
  
  // Extract total_offset_kg (value) and owner
  let value: number
  
  // Try different possible field names
  if (fields.total_offset_kg !== undefined) {
    value = typeof fields.total_offset_kg === "string" 
      ? parseInt(fields.total_offset_kg, 10) 
      : fields.total_offset_kg
  } else if (fields.value !== undefined) {
    value = typeof fields.value === "string" 
      ? parseInt(fields.value, 10) 
      : fields.value
  } else {
    console.log("Value field not found in object")
    return null
  }
  
  if (isNaN(value)) {
    console.log("Value is not a valid number")
    return null
  }
  
  // Extract owner
  if (!fields.owner) {
    console.log("Owner field is missing")
    return null
  }
  
  const owner = String(fields.owner)
  
  return {
    value,
    owner,
  }
}

// ============================================================================
// INTERFACE DEFINITIONS
// ============================================================================

export interface ContractData {
  value: number
  owner: string
}

export interface ContractState {
  isLoading: boolean
  isPending: boolean
  isConfirming: boolean
  isConfirmed: boolean
  hash: string | undefined
  error: Error | null
}

export interface ContractActions {
  createObject: () => Promise<void>
  addOffset: (amount: number) => Promise<void>
  transferOffset: (to: string, amount: number) => Promise<void>
  increment: () => Promise<void>
  setValue: (value: number) => Promise<void>
  getTotalOffset: () => Promise<number | null>
  getOwner: () => Promise<string | null>
  clearObject: () => void
}

// ============================================================================
// MAIN HOOK
// ============================================================================

export const useContract = () => {
  const currentAccount = useCurrentAccount()
  const address = currentAccount?.address
  const packageId = useNetworkVariable("packageId")
  const iotaClient = useIotaClient()
  const { mutate: signAndExecute, isPending } = useSignAndExecuteTransaction()
  const [objectId, setObjectId] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [hash, setHash] = useState<string | undefined>()
  const [transactionError, setTransactionError] = useState<Error | null>(null)

  // Load object ID from URL hash
  useEffect(() => {
    if (typeof window !== "undefined") {
      const hash = window.location.hash.slice(1)
      if (hash) setObjectId(hash)
    }
  }, [])

  // Fetch object data
  const { data, isPending: isFetching, error: queryError, refetch } = useIotaClientQuery(
    "getObject",
    {
      id: objectId!,
      options: { showContent: true, showOwner: true },
    },
    {
      enabled: !!objectId,
    }
  )

  // Extract fields
  const fields = data?.data ? getObjectFields(data.data) : null
  const isOwner = fields?.owner.toLowerCase() === address?.toLowerCase()
  const objectExists = !!data?.data
  const hasValidData = !!fields

  // ============================================================================
  // HELPER FUNCTIONS FOR TRANSACTION CREATION
  // ============================================================================

  // Helper to create moveCall transaction
  const createMoveCallTx = (target: string, args: any[]) => {
    // Dynamically import Transaction to avoid import issues
    const Transaction = require("@iota/iota-sdk/transactions").Transaction;
    const tx = new Transaction();
    tx.moveCall({
      target,
      arguments: args,
    });
    return tx;
  };

  // Helper to create object argument
  const txObject = (objectId: string) => {
    const Transaction = require("@iota/iota-sdk/transactions").Transaction;
    const tx = new Transaction();
    return tx.object(objectId);
  };

  // Helper to create pure arguments
  const txPure = {
    u64: (value: number) => {
      const Transaction = require("@iota/iota-sdk/transactions").Transaction;
      const tx = new Transaction();
      return tx.pure.u64(value);
    },
    address: (value: string) => {
      const Transaction = require("@iota/iota-sdk/transactions").Transaction;
      const tx = new Transaction();
      return tx.pure.address(value);
    }
  };

  // ============================================================================
  // CONTRACT FUNCTIONS
  // ============================================================================

  // Create object
  const createObject = async () => {
    if (!packageId) return

    try {
      setIsLoading(true)
      setTransactionError(null)
      setHash(undefined)
      
      const tx = createMoveCallTx(
        `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.CREATE}`,
        []
      );

      signAndExecute(
        { transaction: tx },
        {
          onSuccess: async ({ digest }) => {
            setHash(digest)
            try {
              const { effects } = await iotaClient.waitForTransaction({
                digest,
                options: { showEffects: true },
              })
              const newObjectId = effects?.created?.[0]?.reference?.objectId
              if (newObjectId) {
                setObjectId(newObjectId)
                if (typeof window !== "undefined") {
                  window.location.hash = newObjectId
                }
                setIsLoading(false)
              } else {
                setIsLoading(false)
                console.warn("No object ID found in transaction effects")
              }
            } catch (waitError) {
              console.error("Error waiting for transaction:", waitError)
              setIsLoading(false)
            }
          },
          onError: (err) => {
            const error = err instanceof Error ? err : new Error(String(err))
            setTransactionError(error)
            console.error("Error:", err)
            setIsLoading(false)
          },
        }
      )
    } catch (err) {
      const error = err instanceof Error ? err : new Error(String(err))
      setTransactionError(error)
      console.error("Error creating object:", err)
      setIsLoading(false)
    }
  }

  // Add carbon offset
  const addOffset = async (amount: number) => {
    if (!objectId || !packageId) return

    try {
      setIsLoading(true)
      setTransactionError(null)
      const tx = createMoveCallTx(
        `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.ADD_OFFSET}`,
        [txObject(objectId), txPure.u64(amount)]
      );

      signAndExecute(
        { transaction: tx },
        {
          onSuccess: async ({ digest }) => {
            setHash(digest)
            await iotaClient.waitForTransaction({ digest })
            await refetch()
            setIsLoading(false)
          },
          onError: (err) => {
            const error = err instanceof Error ? err : new Error(String(err))
            setTransactionError(error)
            console.error("Error adding offset:", err)
            setIsLoading(false)
          },
        }
      )
    } catch (err) {
      const error = err instanceof Error ? err : new Error(String(err))
      setTransactionError(error)
      console.error("Error adding offset:", err)
      setIsLoading(false)
    }
  }

  // Transfer offset to another user
  const transferOffset = async (to: string, amount: number) => {
    if (!objectId || !packageId) return

    try {
      setIsLoading(true)
      setTransactionError(null)
      const tx = createMoveCallTx(
        `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.TRANSFER_OFFSET}`,
        [txObject(objectId), txPure.address(to), txPure.u64(amount)]
      );

      signAndExecute(
        { transaction: tx },
        {
          onSuccess: async ({ digest }) => {
            setHash(digest)
            await iotaClient.waitForTransaction({ digest })
            await refetch()
            setIsLoading(false)
          },
          onError: (err) => {
            const error = err instanceof Error ? err : new Error(String(err))
            setTransactionError(error)
            console.error("Error transferring offset:", err)
            setIsLoading(false)
          },
        }
      )
    } catch (err) {
      const error = err instanceof Error ? err : new Error(String(err))
      setTransactionError(error)
      console.error("Error transferring offset:", err)
      setIsLoading(false)
    }
  }

  // Increment (for testing)
  const increment = async () => {
    if (!objectId || !packageId) return

    try {
      setIsLoading(true)
      setTransactionError(null)
      const tx = createMoveCallTx(
        `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.INCREMENT}`,
        [txObject(objectId)]
      );

      signAndExecute(
        { transaction: tx },
        {
          onSuccess: async ({ digest }) => {
            setHash(digest)
            await iotaClient.waitForTransaction({ digest })
            await refetch()
            setIsLoading(false)
          },
          onError: (err) => {
            const error = err instanceof Error ? err : new Error(String(err))
            setTransactionError(error)
            console.error("Error incrementing:", err)
            setIsLoading(false)
          },
        }
      )
    } catch (err) {
      const error = err instanceof Error ? err : new Error(String(err))
      setTransactionError(error)
      console.error("Error incrementing:", err)
      setIsLoading(false)
    }
  }

  // Set value (admin only)
  const setValue = async (value: number) => {
    if (!objectId || !packageId) return

    try {
      setIsLoading(true)
      setTransactionError(null)
      const tx = createMoveCallTx(
        `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.SET_VALUE}`,
        [txObject(objectId), txPure.u64(value)]
      );

      signAndExecute(
        { transaction: tx },
        {
          onSuccess: async ({ digest }) => {
            setHash(digest)
            await iotaClient.waitForTransaction({ digest })
            await refetch()
            setIsLoading(false)
          },
          onError: (err) => {
            const error = err instanceof Error ? err : new Error(String(err))
            setTransactionError(error)
            console.error("Error setting value:", err)
            setIsLoading(false)
          },
        }
      )
    } catch (err) {
      const error = err instanceof Error ? err : new Error(String(err))
      setTransactionError(error)
      console.error("Error setting value:", err)
      setIsLoading(false)
    }
  }

  // Get total offset (view function)
  const getTotalOffset = async (): Promise<number | null> => {
    if (!objectId || !packageId) return null

    try {
      // For view functions, we can use the existing data
      return fields?.value || null
    } catch (err) {
      console.error("Error getting total offset:", err)
      return null
    }
  }

  // Get owner (view function)
  const getOwner = async (): Promise<string | null> => {
    if (!objectId || !packageId) return null

    try {
      return fields?.owner || null
    } catch (err) {
      console.error("Error getting owner:", err)
      return null
    }
  }

  // Clear object
  const clearObject = () => {
    setObjectId(null)
    setTransactionError(null)
    if (typeof window !== "undefined") {
      window.location.hash = ""
    }
  }

  // ============================================================================
  // RETURN VALUES
  // ============================================================================

  const contractData: ContractData | null = fields
    ? {
        value: fields.value,
        owner: fields.owner,
      }
    : null

  const actions: ContractActions = {
    createObject,
    addOffset,
    transferOffset,
    increment,
    setValue,
    getTotalOffset,
    getOwner,
    clearObject,
  }

  const contractState: ContractState = {
    isLoading: (isLoading && !objectId) || isPending || isFetching,
    isPending,
    isConfirming: false,
    isConfirmed: !!hash && !isLoading && !isPending,
    hash,
    error: queryError || transactionError,
  }

  // Simple wallet connect/disconnect (dApp Kit handles this automatically)
  const connect = () => {
    console.log("Wallet connection is handled by dApp Kit - check if wallet extension is installed")
  }

  const disconnect = () => {
    console.log("Wallet disconnection is handled by dApp Kit")
  }

  return {
    // Account info
    account: currentAccount,
    address,
    packageId,
    
    // Contract data
    data: contractData,
    contractData, // alias for compatibility
    
    // Actions
    actions,
    ...actions, // Spread actions for direct access
    
    // State
    state: contractState,
    loading: contractState.isLoading,
    error: contractState.error,
    transactionResult: hash ? { digest: hash } : null,
    
    // Object info
    objectId,
    isOwner,
    objectExists,
    hasValidData,
    
    // Wallet functions
    connect,
    disconnect,
    
    // Helper functions
    getTotalOffset: actions.getTotalOffset,
    getOwner: actions.getOwner,
  }
}

// Type export for convenience
export type UseContractReturn = ReturnType<typeof useContract>