"use client"

interface CarbonActionsProps {
  isOwner: boolean
  objectExists: boolean
  loading: boolean
  setValueAmount: string
  onSetValueAmountChange: (value: string) => void
  onSetValue: () => void
  onIncrement: () => void
}

export default function CarbonActions({
  isOwner,
  objectExists,
  loading,
  setValueAmount,
  onSetValueAmountChange,
  onSetValue,
  onIncrement
}: CarbonActionsProps) {
  if (!objectExists || !isOwner) return null

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <h2 className="text-xl font-semibold text-gray-800 mb-4">
        Administration
      </h2>
      <p className="text-sm text-gray-500 mb-6">
        Owner-only functions for managing carbon offset data
      </p>
      
      <div className="space-y-6">
        {/* Set Value Form */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Update Total Offset
          </label>
          <div className="flex gap-3">
            <input
              type="number"
              value={setValueAmount}
              onChange={(e) => onSetValueAmountChange(e.target.value)}
              placeholder="New total value (kg)"
              className="flex-1 border border-gray-300 rounded-lg px-4 py-2.5 focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent"
              min="0"
              step="1"
            />
            <button
              onClick={onSetValue}
              disabled={loading || !setValueAmount || parseInt(setValueAmount) < 0}
              className="bg-amber-500 hover:bg-amber-600 text-white px-5 py-2.5 rounded-lg font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-colors whitespace-nowrap"
            >
              Update
            </button>
          </div>
          <p className="text-xs text-gray-400 mt-2">
            Caution: This will overwrite the current total value
          </p>
        </div>

        {/* Quick Actions */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-3">
            Quick Actions
          </label>
          <div className="grid grid-cols-2 gap-3">
            <button
              onClick={onIncrement}
              disabled={loading}
              className="bg-purple-500 hover:bg-purple-600 text-white px-4 py-3 rounded-lg font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-center"
            >
              +1 kg (Test)
            </button>
            <button
              onClick={() => onSetValueAmountChange('1000')}
              className="bg-gray-100 hover:bg-gray-200 text-gray-800 px-4 py-3 rounded-lg font-medium transition-colors text-center"
            >
              Set 1000 kg
            </button>
          </div>
        </div>

        {/* Info Box */}
        <div className="bg-blue-50 border border-blue-100 rounded-lg p-4">
          <div className="flex items-start gap-3">
            <span className="text-blue-500 text-lg">‚ÑπÔ∏è</span>
            <div>
              <p className="text-sm font-medium text-blue-800">
                Owner Privileges
              </p>
              <p className="text-xs text-blue-600 mt-1">
                As the object owner, you can modify the total offset value and test increment functionality.
                These changes are permanent and recorded on-chain.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}