"use client"

import { useState } from "react"
import { WalletConnect } from "@/components/Wallet-connect"
import { useContract } from "@/hooks/useContract"
import CarbonStats from "@/components/CarbonStats"
import CarbonActions from "@/components/CarbonActions"
import TransactionStatus from "@/components/TransactionStatus"
import ObjectManagement from "@/components/ObjectManagement"

export default function CarbonDashboard() {
  const {
    address,
    data,
    objectId,
    isOwner,
    objectExists,
    loading,
    error,
    actions,
    state
  } = useContract()

  const [offsetAmount, setOffsetAmount] = useState('')
  const [transferTo, setTransferTo] = useState('')
  const [transferAmount, setTransferAmount] = useState('')
  const [setValueAmount, setSetValueAmount] = useState('')

  // N·∫øu ch∆∞a k·∫øt n·ªëi v√≠
  if (!address) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-emerald-50 to-blue-50 flex items-center justify-center p-4">
        <div className="text-center p-8 bg-white rounded-2xl shadow-lg max-w-md w-full">
          <div className="w-20 h-20 bg-emerald-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <span className="text-3xl">üå±</span>
          </div>
          <h1 className="text-2xl font-bold text-gray-800 mb-2">
            Carbon Offset Tracker
          </h1>
          <p className="text-gray-600 mb-6">
            Track and manage micro carbon offsets on IOTA blockchain
          </p>
          <WalletConnect />
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-emerald-50 to-blue-50">
      {/* Header */}
      <header className="sticky top-0 z-50 bg-white/80 backdrop-blur-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-emerald-100 rounded-lg flex items-center justify-center">
                <span className="text-xl">üåç</span>
              </div>
              <div>
                <h1 className="text-xl font-bold text-gray-800">
                  Carbon Offset Tracker
                </h1>
                <p className="text-xs text-gray-500">
                  {address.slice(0, 8)}...{address.slice(-6)}
                </p>
              </div>
            </div>
            <WalletConnect />
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Transaction Status */}
        {(state.hash || error) && (
          <div className="mb-6">
            <TransactionStatus 
              hash={state.hash}
              error={error}
              isLoading={loading}
            />
          </div>
        )}

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Left Column - Stats & Info */}
          <div className="lg:col-span-2 space-y-6">
            {/* Carbon Stats Card */}
            <CarbonStats 
              data={data}
              objectId={objectId}
              objectExists={objectExists}
              isOwner={isOwner}
              loading={loading}
              onCreateObject={actions.createObject}
            />

            {/* Add Offset Section */}
            {objectExists && isOwner && (
              <div className="bg-white rounded-xl shadow-lg p-6">
                <h2 className="text-xl font-semibold text-gray-800 mb-4">
                  Add Carbon Offset
                </h2>
                <div className="flex flex-col sm:flex-row gap-4">
                  <input
                    type="number"
                    value={offsetAmount}
                    onChange={(e) => setOffsetAmount(e.target.value)}
                    placeholder="Enter offset amount in kg"
                    className="flex-1 border border-gray-300 rounded-lg px-4 py-3 focus:outline-none focus:ring-2 focus:ring-emerald-500 focus:border-transparent"
                    min="1"
                    step="1"
                  />
                  <button
                    onClick={() => {
                      const amount = parseInt(offsetAmount)
                      if (amount > 0) {
                        actions.addOffset(amount)
                        setOffsetAmount('')
                      }
                    }}
                    disabled={loading || !offsetAmount || parseInt(offsetAmount) <= 0}
                    className="bg-emerald-500 hover:bg-emerald-600 text-white px-6 py-3 rounded-lg font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                  >
                    {loading ? 'Processing...' : 'Add Offset'}
                  </button>
                </div>
                <p className="text-sm text-gray-500 mt-3">
                  Add the amount of carbon you've offset in kilograms
                </p>
              </div>
            )}

            {/* Transfer Section */}
            {objectExists && isOwner && (
              <div className="bg-white rounded-xl shadow-lg p-6">
                <h2 className="text-xl font-semibold text-gray-800 mb-4">
                  Transfer Offset
                </h2>
                <div className="space-y-4">
                  <input
                    type="text"
                    value={transferTo}
                    onChange={(e) => setTransferTo(e.target.value)}
                    placeholder="Recipient wallet address"
                    className="w-full border border-gray-300 rounded-lg px-4 py-3 focus:outline-none focus:ring-2 focus:ring-emerald-500 focus:border-transparent"
                  />
                  <input
                    type="number"
                    value={transferAmount}
                    onChange={(e) => setTransferAmount(e.target.value)}
                    placeholder="Amount to transfer (kg)"
                    className="w-full border border-gray-300 rounded-lg px-4 py-3 focus:outline-none focus:ring-2 focus:ring-emerald-500 focus:border-transparent"
                    min="1"
                    step="1"
                  />
                  <button
                    onClick={() => {
                      const amount = parseInt(transferAmount)
                      if (transferTo && amount > 0) {
                        actions.transferOffset(transferTo, amount)
                        setTransferTo('')
                        setTransferAmount('')
                      }
                    }}
                    disabled={loading || !transferTo || !transferAmount || parseInt(transferAmount) <= 0}
                    className="w-full bg-blue-500 hover:bg-blue-600 text-white px-6 py-3 rounded-lg font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                  >
                    {loading ? 'Processing...' : 'Transfer Offset'}
                  </button>
                </div>
              </div>
            )}
          </div>

          {/* Right Column - Actions & Management */}
          <div className="space-y-6">
            <CarbonActions 
              isOwner={isOwner}
              objectExists={objectExists}
              loading={loading}
              setValueAmount={setValueAmount}
              onSetValueAmountChange={setSetValueAmount}
              onSetValue={() => {
                const value = parseInt(setValueAmount)
                if (value >= 0) {
                  actions.setValue(value)
                  setSetValueAmount('')
                }
              }}
              onIncrement={actions.increment}
            />

            <ObjectManagement 
              objectExists={objectExists}
              loading={loading}
              onRefresh={actions.getTotalOffset}
              onClearObject={actions.clearObject}
              onCreateObject={actions.createObject}
            />

            {/* Stats Panel */}
            <div className="bg-white rounded-xl shadow-lg p-6">
              <h2 className="text-xl font-semibold text-gray-800 mb-4">
                System Status
              </h2>
              <div className="space-y-3">
                <div className="flex justify-between items-center py-2 border-b border-gray-100">
                  <span className="text-gray-600">Wallet Connected</span>
                  <span className="text-green-600 font-medium">‚úì</span>
                </div>
                <div className="flex justify-between items-center py-2 border-b border-gray-100">
                  <span className="text-gray-600">Object Exists</span>
                  <span className={objectExists ? 'text-green-600' : 'text-red-600'}>
                    {objectExists ? '‚úì' : '‚úó'}
                  </span>
                </div>
                <div className="flex justify-between items-center py-2 border-b border-gray-100">
                  <span className="text-gray-600">Ownership</span>
                  <span className={isOwner ? 'text-green-600' : 'text-amber-600'}>
                    {isOwner ? 'Owner' : 'Not Owner'}
                  </span>
                </div>
                <div className="flex justify-between items-center py-2">
                  <span className="text-gray-600">Network</span>
                  <span className="text-blue-600 font-medium">IOTA Devnet</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Footer */}
        <footer className="mt-12 pt-8 border-t border-gray-200">
          <div className="flex flex-col md:flex-row justify-between items-center">
            <div className="mb-4 md:mb-0">
              <div className="flex items-center gap-2">
                <div className="w-8 h-8 bg-emerald-100 rounded-lg flex items-center justify-center">
                  <span className="text-lg">üåø</span>
                </div>
                <span className="font-semibold text-gray-800">Carbon Offset Tracker</span>
              </div>
              <p className="text-sm text-gray-500 mt-2">
                Building a greener future on IOTA
              </p>
            </div>
            <div className="text-sm text-gray-500">
              <p>¬© {new Date().getFullYear()} Micro Carbon Offset Tracker</p>
              <p className="mt-1">Powered by IOTA Blockchain</p>
            </div>
          </div>
        </footer>
      </main>
    </div>
  )
}