"use client"

import { ContractData } from "@/hooks/useContract"

interface CarbonStatsProps {
  data: ContractData | null
  objectId: string | null
  objectExists: boolean
  isOwner: boolean
  loading: boolean
  onCreateObject: () => void
}

export default function CarbonStats({
  data,
  objectId,
  objectExists,
  isOwner,
  loading,
  onCreateObject
}: CarbonStatsProps) {
  if (!objectExists) {
    return (
      <div className="bg-white rounded-xl shadow-lg p-8 text-center">
        <div className="w-20 h-20 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-6">
          <span className="text-3xl">üìù</span>
        </div>
        <h3 className="text-2xl font-bold text-gray-700 mb-3">
          No Carbon Object Found
        </h3>
        <p className="text-gray-500 mb-8 max-w-md mx-auto">
          Create a carbon object to start tracking your carbon offsets on the IOTA blockchain.
          This will create a unique digital asset representing your carbon offset contributions.
        </p>
        <button
          onClick={onCreateObject}
          disabled={loading}
          className="bg-emerald-500 hover:bg-emerald-600 text-white px-8 py-4 rounded-lg font-semibold text-lg disabled:opacity-50 disabled:cursor-not-allowed transition-colors shadow-md hover:shadow-lg"
        >
          {loading ? 'Creating Object...' : 'Create Carbon Object'}
        </button>
      </div>
    )
  }

  if (!data) {
    return (
      <div className="bg-white rounded-xl shadow-lg p-8 text-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-emerald-500 mx-auto"></div>
        <p className="text-gray-500 mt-4">Loading carbon data...</p>
      </div>
    )
  }

  return (
    <div className="bg-white rounded-xl shadow-lg overflow-hidden">
      <div className="p-6 border-b border-gray-100">
        <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
          <div>
            <h2 className="text-2xl font-bold text-gray-800">Carbon Offset Summary</h2>
            <p className="text-gray-500">Your carbon offset contributions</p>
          </div>
          {objectId && (
            <div className="bg-emerald-50 text-emerald-800 px-4 py-2 rounded-lg">
              <p className="text-sm font-medium">Object ID</p>
              <p className="font-mono text-sm truncate max-w-xs">
                {objectId}
              </p>
            </div>
          )}
        </div>
      </div>

      <div className="p-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {/* Total Offset Card */}
          <div className="bg-gradient-to-br from-emerald-50 to-green-50 border border-emerald-100 rounded-xl p-6">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm text-emerald-600 font-medium uppercase tracking-wider">
                  Total Carbon Offset
                </p>
                <p className="text-4xl font-bold text-gray-800 mt-3">
                  {data.value.toLocaleString()}
                  <span className="text-lg text-gray-600 ml-2">kg</span>
                </p>
                <div className="mt-4">
                  <p className="text-emerald-700">
                    ‚âà {(data.value / 1000).toFixed(2)} tons of CO‚ÇÇ
                  </p>
                  <p className="text-sm text-gray-500 mt-1">
                    Equivalent to {Math.round(data.value / 21)} trees planted
                  </p>
                </div>
              </div>
              <div className="w-14 h-14 bg-emerald-100 rounded-full flex items-center justify-center">
                <span className="text-2xl">üåø</span>
              </div>
            </div>
          </div>

          {/* Owner Info Card */}
          <div className="bg-gradient-to-br from-blue-50 to-cyan-50 border border-blue-100 rounded-xl p-6">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm text-blue-600 font-medium uppercase tracking-wider">
                  Ownership Details
                </p>
                <div className="mt-3">
                  <p className="text-sm text-gray-600">Current Owner</p>
                  <p className="font-mono text-sm text-gray-800 break-all mt-1">
                    {data.owner}
                  </p>
                </div>
                <div className="mt-4">
                  <div className={`inline-flex items-center gap-2 px-3 py-1 rounded-full ${isOwner ? 'bg-green-100 text-green-800' : 'bg-amber-100 text-amber-800'}`}>
                    <span className="w-2 h-2 rounded-full bg-current"></span>
                    <span className="text-sm font-medium">
                      {isOwner ? 'You are the owner' : 'Not the owner'}
                    </span>
                  </div>
                </div>
              </div>
              <div className="w-14 h-14 bg-blue-100 rounded-full flex items-center justify-center">
                <span className="text-2xl">üë§</span>
              </div>
            </div>
          </div>
        </div>

        {/* Environmental Impact */}
        <div className="mt-8 pt-6 border-t border-gray-100">
          <h3 className="text-lg font-semibold text-gray-800 mb-4">
            Environmental Impact
          </h3>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-gray-50 rounded-lg">
              <p className="text-2xl font-bold text-emerald-600">
                {Math.round(data.value / 21)}
              </p>
              <p className="text-sm text-gray-600">Trees Equivalent</p>
            </div>
            <div className="text-center p-4 bg-gray-50 rounded-lg">
              <p className="text-2xl font-bold text-emerald-600">
                {(data.value / 15000).toFixed(1)}
              </p>
              <p className="text-sm text-gray-600">Cars Off Road (days)</p>
            </div>
            <div className="text-center p-4 bg-gray-50 rounded-lg">
              <p className="text-2xl font-bold text-emerald-600">
                {(data.value / 84).toFixed(0)}
              </p>
              <p className="text-sm text-gray-600">Smartphones Charged</p>
            </div>
            <div className="text-center p-4 bg-gray-50 rounded-lg">
              <p className="text-2xl font-bold text-emerald-600">
                {(data.value / 170).toFixed(0)}
              </p>
              <p className="text-sm text-gray-600">Lightbulbs (hours)</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}