"use client"

interface TransactionStatusProps {
  hash?: string
  error: Error | null
  isLoading: boolean
}

export default function TransactionStatus({
  hash,
  error,
  isLoading
}: TransactionStatusProps) {
  if (isLoading) {
    return (
      <div className="bg-blue-50 border border-blue-200 rounded-xl p-4">
        <div className="flex items-center gap-3">
          <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-blue-500"></div>
          <div>
            <p className="font-medium text-blue-800">Processing Transaction</p>
            <p className="text-sm text-blue-600">Please wait while we confirm your transaction...</p>
          </div>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-xl p-4">
        <div className="flex items-start gap-3">
          <span className="text-red-500 text-xl">‚ö†Ô∏è</span>
          <div className="flex-1">
            <p className="font-medium text-red-800">Transaction Failed</p>
            <p className="text-sm text-red-600 mt-1">{error.message}</p>
            <p className="text-xs text-red-500 mt-2">
              Please check your wallet balance and try again
            </p>
          </div>
        </div>
      </div>
    )
  }

  if (hash) {
    return (
      <div className="bg-green-50 border border-green-200 rounded-xl p-4">
        <div className="flex items-start gap-3">
          <span className="text-green-500 text-xl">‚úÖ</span>
          <div className="flex-1">
            <p className="font-medium text-green-800">Transaction Successful</p>
            <p className="text-sm text-green-600 mt-1">
              Your transaction has been confirmed on the blockchain
            </p>
            <div className="mt-3">
              <p className="text-xs font-medium text-gray-600">Transaction Hash:</p>
              <p className="text-xs font-mono text-gray-700 break-all mt-1">
                {hash}
              </p>
              <a
                href={`https://explorer.iota.org/devnet/txblock/${hash}`}
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center gap-1 text-xs text-blue-500 hover:text-blue-600 mt-2"
              >
                View on Explorer
                <span>‚Üó</span>
              </a>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return null
}