"use client"

interface ObjectManagementProps {
  objectExists: boolean
  loading: boolean
  onRefresh: () => void
  onClearObject: () => void
  onCreateObject: () => void
}

export default function ObjectManagement({
  objectExists,
  loading,
  onRefresh,
  onClearObject,
  onCreateObject
}: ObjectManagementProps) {
  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <h2 className="text-xl font-semibold text-gray-800 mb-4">
        Object Management
      </h2>
      
      <div className="space-y-4">
        {objectExists ? (
          <>
            <button
              onClick={onRefresh}
              disabled={loading}
              className="w-full bg-gray-50 hover:bg-gray-100 border border-gray-300 text-gray-800 px-4 py-3 rounded-lg font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center gap-2"
            >
              <span>üîÑ</span>
              Refresh Object Data
            </button>
            
            <div className="border-t border-gray-200 pt-4">
              <p className="text-sm text-gray-500 mb-3">Danger Zone</p>
              <button
                onClick={onClearObject}
                className="w-full bg-red-50 hover:bg-red-100 border border-red-200 text-red-600 px-4 py-3 rounded-lg font-medium transition-colors flex items-center justify-center gap-2"
              >
                <span>üóëÔ∏è</span>
                Clear Object from Memory
              </button>
              <p className="text-xs text-gray-400 mt-2 text-center">
                Note: This only removes the object from local memory, not from the blockchain
              </p>
            </div>
          </>
        ) : (
          <button
            onClick={onCreateObject}
            disabled={loading}
            className="w-full bg-emerald-500 hover:bg-emerald-600 text-white px-6 py-4 rounded-lg font-semibold disabled:opacity-50 disabled:cursor-not-allowed transition-colors shadow-md hover:shadow-lg flex items-center justify-center gap-2"
          >
            <span>‚ú®</span>
            Create New Carbon Object
          </button>
        )}
      </div>

      <div className="mt-6 pt-6 border-t border-gray-100">
        <h3 className="text-sm font-medium text-gray-700 mb-3">
          What is a Carbon Object?
        </h3>
        <ul className="text-xs text-gray-500 space-y-2">
          <li className="flex items-start gap-2">
            <span className="text-emerald-500 mt-0.5">‚Ä¢</span>
            <span>A digital asset on IOTA blockchain tracking your carbon offsets</span>
          </li>
          <li className="flex items-start gap-2">
            <span className="text-emerald-500 mt-0.5">‚Ä¢</span>
            <span>Immutable record of your environmental contributions</span>
          </li>
          <li className="flex items-start gap-2">
            <span className="text-emerald-500 mt-0.5">‚Ä¢</span>
            <span>Transferable to other users</span>
          </li>
          <li className="flex items-start gap-2">
            <span className="text-emerald-500 mt-0.5">‚Ä¢</span>
            <span>Stored permanently on the IOTA network</span>
          </li>
        </ul>
      </div>
    </div>
  )
}